import React, { useMemo, useState } from 'react';
import YuzuLogo from '../components/YuzuLogo';

type TrailDot = {
  id: number;
  x: number;
  y: number;
};

const Welcome: React.FC = () => {
  const [dots, setDots] = useState<TrailDot[]>([]);

  const now = useMemo(() => new Date(), []);
  const hour = now.getHours();

  let greeting = '';
  if (hour < 11) greeting = '';
  else if (hour < 14) greeting = '';
  else if (hour < 18) greeting = '';
  else greeting = '';

  const handlePointer = (clientX: number, clientY: number) => {
    const id = Date.now() + Math.random();
    setDots((prev) => [...prev.slice(-20), { id, x: clientX, y: clientY }]);
    window.setTimeout(() => {
      setDots((prev) => prev.filter((d) => d.id !== id));
    }, 600);
  };

  return (
    <div
      className="welcome-screen"
      onTouchMove={(e) => {
        const t = e.touches[0];
        if (t) {
          handlePointer(t.clientX, t.clientY);
        }
      }}
      onClick={(e) => {
        handlePointer(e.clientX, e.clientY);
      }}
    >
      <div className="welcome-content">
        <div style={{ marginBottom: 24 }}>
          <YuzuLogo subtitle="" />
        </div>
        <div className="welcome-greeting">
          <div className="welcome-greeting-main">{greeting}</div>
          <div className="welcome-greeting-sub"></div>
        </div>
        <div className="welcome-bubbles">
          <div className="welcome-bubble bubble-1"> </div>
          <div className="welcome-bubble bubble-2"> </div>
          <div className="welcome-bubble bubble-3"> </div>
          <div className="welcome-bubble bubble-4"> </div>
        </div>
        <div className="welcome-tip"></div>
      </div>
      <div className="welcome-trails">
        {dots.map((dot) => (
          <span
            key={dot.id}
            className="welcome-star"
            style={{
              left: dot.x,
              top: dot.y
            }}
          />
        ))}
      </div>
    </div>
  );
};

export default Welcome;

