import React, { useEffect, useMemo, useState } from 'react';
import { Alert, Button, Card, Col, Dropdown, Form, Radio, Row, Space, Tag, message } from 'antd';
import { MoreOutlined, SoundOutlined, CheckCircleTwoTone, CloseCircleTwoTone } from '@ant-design/icons';
import { playWord, playCongratulations, isSpeechSupported } from '../utils/speech';
import YuzuLogo from '../components/YuzuLogo';

type Settings = {
  includeUpperCase: boolean;
  includeLowerCase: boolean;
};

type Problem = {
  letter: string;
  options: string[];
  answer: string;
  picked?: string;
};

const defaultSettings: Settings = {
  includeLowerCase: true,
  includeUpperCase: true
};

const similarLetters: Record<string, string[]> = {
  A: ['a', 'R', 'H', 'V'],
  B: ['b', 'P', 'R', 'D'],
  C: ['c', 'G', 'O', 'Q'],
  D: ['d', 'B', 'P', 'O'],
  E: ['e', 'F', 'B', 'P'],
  F: ['f', 'E', 'P', 'T'],
  G: ['g', 'C', 'O', 'Q'],
  H: ['h', 'A', 'N', 'M'],
  I: ['i', 'l', 'T', 'J'],
  J: ['j', 'I', 'L', 'T'],
  K: ['k', 'R', 'X', 'Y'],
  L: ['l', 'I', 'T', 'J'],
  M: ['m', 'N', 'W', 'H'],
  N: ['n', 'M', 'H', 'U'],
  O: ['o', 'Q', 'C', 'G'],
  P: ['p', 'B', 'R', 'D'],
  Q: ['q', 'O', 'G', 'C'],
  R: ['r', 'P', 'B', 'K'],
  S: ['s', 'Z', '5', '2'],
  T: ['t', 'I', 'L', 'J'],
  U: ['u', 'V', 'Y', 'N'],
  V: ['v', 'U', 'Y', 'A'],
  W: ['w', 'M', 'V', 'U'],
  X: ['x', 'K', 'Y', 'Z'],
  Y: ['y', 'V', 'U', 'X'],
  Z: ['z', 'S', '2', 'N']
};

const shuffle = <T,>(arr: T[]) => {
  const copy = [...arr];
  for (let i = copy.length - 1; i > 0; i--) {
    const j = Math.floor(Math.random() * (i + 1));
    [copy[i], copy[j]] = [copy[j], copy[i]];
  }
  return copy;
};

const generateRandomLetter = (upper: boolean, lower: boolean) => {
  const letters: string[] = [];
  if (upper) letters.push(...'ABCDEFGHIJKLMNOPQRSTUVWXYZ'.split(''));
  if (lower) letters.push(...'abcdefghijklmnopqrstuvwxyz'.split(''));
  return letters[Math.floor(Math.random() * letters.length)];
};

const generateSimilarLetters = (letter: string) => {
  const upper = letter.toUpperCase();
  const lower = letter.toLowerCase();
  const options = new Set<string>([letter, upper, lower]);
  const similar = similarLetters[upper] || [];
  while (options.size < 4) {
    options.add(similar[Math.floor(Math.random() * similar.length)] || upper);
  }
  return shuffle(Array.from(options)).slice(0, 4);
};

const storageKey = 'englishSettings';

const English: React.FC = () => {
  const [settings, setSettings] = useState<Settings>(defaultSettings);
  const [problems, setProblems] = useState<Problem[]>([]);
  const [result, setResult] = useState<{ correct: number; total: number } | null>(null);
  const [activePane, setActivePane] = useState<'settings' | 'practice'>('settings');

  useEffect(() => {
    const saved = localStorage.getItem(storageKey);
    if (saved) {
      try {
        setSettings({ ...defaultSettings, ...JSON.parse(saved) });
      } catch {
        setSettings(defaultSettings);
      }
    }
  }, []);

  useEffect(() => {
    localStorage.setItem(storageKey, JSON.stringify(settings));
  }, [settings]);

  const canGenerate = useMemo(() => settings.includeLowerCase || settings.includeUpperCase, [settings]);

  const start = () => {
    if (!canGenerate) {
      message.warning('');
      return;
    }
    const list: Problem[] = [];
    for (let i = 0; i < 10; i++) {
      const letter = generateRandomLetter(settings.includeUpperCase, settings.includeLowerCase);
      list.push({
        letter,
        options: generateSimilarLetters(letter),
        answer: letter
      });
    }
    setProblems(list);
    setResult(null);
  };

  const check = () => {
    if (!problems.length) {
      message.info('');
      return;
    }
    const unanswered = problems.filter((p) => !p.picked);
    if (unanswered.length) {
      message.warning('');
      return;
    }
    const correct = problems.filter((p) => p.picked === p.answer).length;
    setResult({ correct, total: problems.length });
    if (correct === problems.length) {
      playCongratulations();
      message.success('');
    }
  };

  const updatePick = (idx: number, value: string) => {
    setProblems((prev) => {
      const next = [...prev];
      next[idx] = { ...next[idx], picked: value };
      return next;
    });
  };

  const settingsMenu = {
    items: [
      {
        key: 'both',
        label: '',
        onClick: () => setSettings({ includeLowerCase: true, includeUpperCase: true })
      },
      {
        key: 'upper',
        label: '',
        onClick: () => setSettings({ includeLowerCase: false, includeUpperCase: true })
      },
      {
        key: 'lower',
        label: '',
        onClick: () => setSettings({ includeLowerCase: true, includeUpperCase: false })
      }
    ]
  };

  return (
    <div className="page">
      <div style={{ marginBottom: 16, display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
        <YuzuLogo subtitle="" />
        <Dropdown menu={settingsMenu} trigger={['click']}>
          <Button size="small" shape="circle" icon={<MoreOutlined />} />
        </Dropdown>
      </div>
      <Card
        tabList={[
          { key: 'settings', label: '' },
          { key: 'practice', label: '' }
        ]}
        activeTabKey={activePane}
        onTabChange={(key) => {
          const next = key as 'settings' | 'practice';
          if (next === 'practice' && !problems.length) {
            start();
          }
          setActivePane(next);
        }}
      >
        {activePane === 'settings' && (
          <Row gutter={[16, 16]}>
            <Col xs={24} md={10}>
              <Card
                type="inner"
                title=""
                extra={
                  <Space>
                    {settings.includeUpperCase && <Tag color="blue"></Tag>}
                    {settings.includeLowerCase && <Tag color="green"></Tag>}
                    {!isSpeechSupported && <Tag color="red"></Tag>}
                  </Space>
                }
              >
                <Form layout="vertical">
                  <Form.Item label="">
                    <Radio.Group
                      value={settings.includeUpperCase ? (settings.includeLowerCase ? 'both' : 'upper') : 'lower'}
                      onChange={(e) => {
                        const val = e.target.value;
                        if (val === 'both') setSettings({ includeLowerCase: true, includeUpperCase: true });
                        if (val === 'upper') setSettings({ includeLowerCase: false, includeUpperCase: true });
                        if (val === 'lower') setSettings({ includeLowerCase: true, includeUpperCase: false });
                      }}
                    >
                      <Radio.Button value="both"></Radio.Button>
                      <Radio.Button value="upper"></Radio.Button>
                      <Radio.Button value="lower"></Radio.Button>
                    </Radio.Group>
                  </Form.Item>
                  <Space>
                    <Button type="primary" onClick={start}>
                      
                    </Button>
                    <Button onClick={check} disabled={!problems.length}>
                      
                    </Button>
                  </Space>
                </Form>
              </Card>
            </Col>
            <Col xs={24} md={14}>
              {result && (
                <Card type="inner" title="">
                  <Alert
                    type={result.correct === result.total ? 'success' : 'info'}
                    message={`${result.correct * 10}  ${result.correct}/${result.total}`}
                    showIcon
                  />
                </Card>
              )}
              {!result && (
                <Alert
                  style={{ marginTop: 16 }}
                  message=""
                  type="info"
                  showIcon
                />
              )}
            </Col>
          </Row>
        )}

        {activePane === 'practice' && (
          <Card
            type="inner"
            title=""
            extra={<Tag color="purple">{problems.length ? ' 10 ' : ''}</Tag>}
          >
            {!problems.length && (
              <Alert
                message=""
                type="info"
                showIcon
              />
            )}
            {!!problems.length && (
              <Space direction="vertical" style={{ width: '100%' }} size="large">
                {problems.map((p, idx) => {
                  const isCorrect = result && p.picked === p.answer;
                  const isWrong = result && p.picked && p.picked !== p.answer;
                  return (
                    <Card
                      key={idx}
                      size="small"
                      title={
                        <Space>
                          <span> {idx + 1} </span>
                          {isCorrect && <CheckCircleTwoTone twoToneColor="#52c41a" />}
                          {isWrong && <CloseCircleTwoTone twoToneColor="#ff4d4f" />}
                        </Space>
                      }
                      extra={
                        <Button
                          size="small"
                          icon={<SoundOutlined />}
                          onClick={() => playWord(p.letter)}
                          disabled={!isSpeechSupported}
                        >
                          
                        </Button>
                      }
                    >
                      <div style={{ fontSize: 28, fontWeight: 600, marginBottom: 8 }}>{p.letter}</div>
                      <Radio.Group
                        onChange={(e) => updatePick(idx, e.target.value)}
                        value={p.picked}
                        options={p.options.map((o) => ({ label: o, value: o }))}
                        optionType="button"
                        buttonStyle="solid"
                      />
                      {result && isWrong && (
                        <div style={{ marginTop: 8, color: '#ff4d4f' }}>{p.answer}</div>
                      )}
                    </Card>
                  );
                })}
              </Space>
            )}
          </Card>
        )}
      </Card>
    </div>
  );
};

export default English;
